import React, { createContext, useContext, useState } from "react";
import { ThemeProvider as StyledThemeProvider } from "styled-components";

const themes = {
  light: {
    background: "#F7F4EB", //  ,   (  )
    color: "#4A4A4A", // ' - (   )
    navBg: "#EFEADD", //   ( ,   )
    navActive: "#D4C9B6", //  -
    buttonBg: "#8D8D8D", //  
    buttonColor: "#FFFFFF", // 

    borderColor: "#D3D3D3", // -  
    hoverBg: "#EBE6D8", //      (   background)
    accentBg: "#E8E2D4", // '     (  navActive)
    accentColor: "#8D8D8D", //   ( buttonBg)

    successBg: "#D4EDDA", // -  
    successColor: "#28A745", //   
    successText: "#155724", // -   

    dangerBg: "#F8D7DA", // -  
    dangerColor: "#DC3545", //   
    dangerText: "#721c24", // -   

    // :    ""    
    //     (navBg)      (background)
    gradientStart: "#EFEADD",
    gradientEnd: "#F7F4EB",
  },
  dark: {
    background: "#2C2C2C",
    color: "#E0E0E0",
    navBg: "#3A3A3A",
    navActive: "#6B6B6B",
    buttonBg: "#B3B3B3",
    buttonColor: "#2C2C2C",

    borderColor: "#4A4A4A",
    hoverBg: "#4C4C4C",
    accentBg: "#5C5C5C",
    accentColor: "#E0E0E0",

    successBg: "#1F4228",
    successColor: "#9CCC65",
    successText: "#D4EDDA",

    dangerBg: "#5C1C1E",
    dangerColor: "#F8D7DA",
    dangerText: "#FAECEC",

    gradientStart: "#2C2C2C",
    gradientEnd: "#6B6B6B",
  },
};

const ThemeContext = createContext();

export const useTheme = () => useContext(ThemeContext);

export const ThemeProvider = ({ children }) => {
  const [mode, setMode] = useState("light");
  const toggleTheme = () => setMode(mode === "light" ? "dark" : "light");

  return (
    <ThemeContext.Provider value={{ mode, toggleTheme }}>
      <StyledThemeProvider theme={themes[mode]}>{children}</StyledThemeProvider>
    </ThemeContext.Provider>
  );
};
